    linear-gradient(97deg, #0b91d7, #6efc29);
  background-size: 400% 300%;
  position: relative;
  -webkit-animation: change 120s ease-in-out infinite;
          animation: change 120s ease-in-out infinite;
}

@-webkit-keyframes change {
  0% {
    background-position: 0 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 50 50%;
  }
}

@keyframes change {
  0% {
    background-position: 0 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 50 50%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.Pokecard {
  /* border: 2px solid mediumseagreen; */
  /* width: 300px; */
  width: 140px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background-color: rgb(207, 223, 187);
  border-radius: 3px;
  line-height: 1.4;
}

.Pokecard-data {
  font-size: 80%;
  text-transform: capitalize;
  font-family: 'Handlee', cursive;
  letter-spacing: .5px;
}

.Pokecard-title {
  margin-bottom: .75rem;
  font-weight: bold;
  font-size: 93%;
  font-family: 'Philosopher', sans-serif;
  letter-spacing: .5px;
  text-transform: capitalize;
}

.Pokecard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 1rem;
}

.Pokecard img:hover {
  transform: scale(1.1);
  opacity: 0.9;
  transition: all ease 500ms;
}
.DiscardCard {
  width: 165px;
  height: 223px;
  padding: 10px 20px;
  margin: 1rem;
  color: rgb(255, 255, 255);
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  /* background: rgba(156, 168, 146, 0.8); */
  background: rgb(107, 119, 107);
  border-radius: 3px;
  border: 1.5px ridge rgba(0, 0, 0, 0.2);
}

.DiscardCard-title {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-family: "Amatic SC", cursive;
  font-weight: bold;
  letter-spacing: 1px;
}

.DiscardCard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 110px;
