.card-coming-soon h6 {
  padding: 0 20px 5px 20px;
  color: rgb(134, 65, 65);
  font-size: 1rem;
  font-weight: 600;
  font-family: 'Handlee', cursive;
}

.card-coming-soon p {
  padding: 0 20px 5px 20px;
  color: rgb(103, 46, 110);
  font-size: 1.1rem;
  font-family: 'Handlee', cursive;
}

.card-coming-soon .col-8:hover {
  transform: scale(1.15);
  transition: all ease 500ms;
}

.button {
  height: 100px;
  border-radius: 25% !important;
}

.home-button {
  height: 80px;
  border-radius: 25% !important;
  margin-top: 15px;
}

.button:hover,
.home-button:hover {
  transform: scale(1.25);
  opacity: 0.7;
  transition: all ease 500ms;
}

.fa-home {
  font-size: 80px;
}

.home-button .fa-home {
  font-size: 60px;
}

@media (min-width: 768px) {
  .button {
    position: fixed;
    top: 15%;
    left: 10%;
  }
}
